<template>
  <div>

    <div class="pt">
      <h2>Áî±Áà∂Â±§ÂÇ≥Âà∞Â≠êÂ±§ÔºåÁî®props‰æÜÊé•Êî∂Ë≥áÊñô</h2>
      <p>
        Á≠ÜË®òÔºöpropsÊîæÂú®Â≠êÂ±§ÔºåÊé•ÂèóË≥áÊñôÁöÑ‰∏ÄÊñπÔºåÂ∏∂ÂÖ•ÁöÑ<br />props:['status'],
        ÊòØÊåáÂêëÔºàÂÇ≥ÈÄÅË≥áÊñôÁöÑÔºâÁà∂Â±§ÂâçÈù¢ÁöÑ v-bind:status='mystatus'(ÊîæÂú®import
        ÈÄ≤‰æÜÁöÑcomponents)Ôºõ<br />
        ÈÄèÈÅéÁà∂Â±§Ëß∏Áôº‰∫ã‰ª∂@change='changestatus'Ôºõ<br />Â≠êÂ±§Êé•Êî∂Âà∞ÁöÑÊòØhtmlË£°valueÂÄº(option value="one")
      </p>
      <div class="status">
        <p>ÁãÄÊÖãÔºö{{ status }}</p>
      </div>
    </div>

    <div class="pt">
      <h2>ÔºÑemit</h2>

      <p>
        Ëß∏Áôº‰∫ã‰ª∂ÔºåÂÇ≥ÈÄÅË≥áÊñôÁöÑÊñπÊ≥ï„ÄÇËß∏Áôº‰∫ã‰ª∂ÁöÑ‰∏ÄÊñπÔºöself.$emit('pushusername',self.username)Ôºå
        'pushusername'ÁÇ∫‰∫ã‰ª∂Ôºåself.usernameÁÇ∫Ë¶ÅÂÇ≥ÈÄÅÁöÑË≥áÊñô„ÄÇ<br>
        Âú®Êé•ÂèóÊñπÔºö@pushusername="getusername"ÔºågetusernameÁÇ∫Êé•ÂèóÁöÑÊñπÊ≥ïÔºå
        methods:{getusername(name){}}ÔºåË£°Èù¢ÁöÑ nameÔºåÁÇ∫Ëá™Ë®≠ËÆäÊï∏Ë°®ÂÇ≥ÈÄ≤‰æÜÁöÑÂÄº
        Ôºåthis.username = name
      </p>
      <div>
        <input type="text" class="" v-model="username" />
        <button class="btn btn-warning " @click="upusername"    >SENT
           
        </button>
      </div>
    </div>

  </div>
</template>
<script>

export default {
  

  props: ['status'],
  data() {
    return {
      username: 'Jennie',
    }
  },
  methods: {
    upusername() {
      let self = this;
      alert('Ê≠°ËøéÂõû‰æÜÔºÅ'+ self.username);
      self.$emit('pushusername', self.username)
    }
  }
}
</script>
<style >
.status {
  color: #aa0;
}

</style>


